.button {
    margin-top:10px;
    margin-left: 50%;
    transform: translateX(-50%);

    color: white;
    background-color: black;

    display: inline-block;

    height: 40px;
    padding: 10px;
    padding-left: 25px;
    padding-right: 25px;

    border-radius: 15px;
    border-style: none;

    cursor: pointer;
}

.disabled {
    background-color: rgb(199, 199, 199);
    color: gray;
}

.center {
    text-align: center;
}

.main-container {
    padding-top: 100px;
    margin: auto;
    max-width: 700px;
}

@media screen and (max-width: 700px) {
    .main-container {
        padding-left: 15px;
        padding-right: 15px;
        margin: auto;
        max-width: 700px;
    }
}

.partition-name {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 30px;
    margin-top: 30px;
}

.wide-card {
    width: 100%;
    background: rgb(0,158,226);
    /* background: linear-gradient(135deg, rgba(0,158,226,1) 0%, rgba(2,215,228,1) 35%, rgba(0,232,190,1) 100%); */
    background: linear-gradient(-45deg, #20cceb, #20cceb, #00ffc8, #2dbdf1, #23d5ab);
    animation: gradient 45s ease infinite;

    background-size: 400% 400%;
    position: relative;
    
    background-color: turquoise;
    color: white;
    height: 200px;
    
    margin: auto;
    
    border-radius: 20px;
    
    box-shadow: inset 1px 2px 20px 6px rgb(34 60 80 / 20%);
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.wc-amount {
    font-size: 20px;
    font-weight: 200;
    position: relative;
    top:30px;
    left:30px;
}

.wc-system {
    position: absolute;
    left:30px;
    bottom: 20px;

    font-weight: 600;
}

.transaction-card {
    border-radius: 20px;
    /* background-color: whitesmoke; */
    margin-bottom: 10px;

    display: grid;

    grid-template-columns: 75px 1fr 100px;

    grid-template-areas: 
    "img name amount"
    "img desc amount";
}

.ts-img {
    grid-area: img;
    
    background: rgb(222,222,222);
    
    width:75px;
    height:75px;

    border-radius: 20px;
}

.ts-name {
    grid-area: name;

    margin-top: 10px;
    margin-left: 10px;

    font-size: 18px;
    font-weight: 600;
}

.ts-desc {
    margin-left: 10px;
    position: relative;
    top:-10px;

    font-size: 17px;
    font-weight: 400;

    grid-area: desc;
}

.ts-amount {
    grid-area: amount;

    text-align: end;
    
    display: flex;
    justify-content: right;
    align-items: center;
}

.error-panel {
    width: fit-content;
    color: white;
    background-color: red;
    border-radius: 15px;

    margin: auto;
    margin-top: 10px;
    padding: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
}

.success-panel {
    background-color: limegreen;
}

.red {
    color: red;
}

.green {
    color:#00CF21;
}

.big-font {
    font-weight: 600;
}

.input {
    border:none;
    border-bottom: 1px solid #777;
    width: 100%;
    font-size: 17px;
}

.input:focus {
    outline: none;
}

.tc-name {
    margin-top: 10px;
    font-size: 17px;
}

.subtext {
    width: 100%;
    text-align: center;
}

.link {
    color: black;
    text-decoration: none;
}

.link:hover {
    text-decoration: underline;
}

.link:visited {
    color: black;
}

.right {
    float: right;
}

.left {
    float: left;
}