@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
  margin: 0;
  padding: 0;

  font-family: 'Poppins', sans-serif;

  overflow-x: hidden;
  /* border: 1px solid black; */
}

::-webkit-scrollbar {
  display: none;
}
.frame {
    min-height: 100vh;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    position: relative;
}

.frame-content {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    /* height: auto; */
    position: absolute;
    top: 0; bottom: 0; left: 0; right: 0;
    margin: auto;
    height: 400px;
    /* background-color: whitesmoke; */
}

.header {
    text-align: center;
    font-size: 23px;
    font-weight: 700;
    margin-top: 50px;
    margin-bottom: 50px;
}

.container-info {
    max-width: 600px;

    position: relative;
    left:50%;
    transform: translateX(-50%);
}

.ci-header {
    margin-top: 30px;
    font-size: 20px;
}

@media screen and (max-width: 800px) {
    .ci-header {
        margin-top: 10px;
    }
    .container-info {
        margin-left: 10px;
        margin-right: 10px;
    }
  }
.card-panel {
    background: rgb(0,158,226);
    /* background: linear-gradient(135deg, rgba(0,158,226,1) 0%, rgba(2,215,228,1) 35%, rgba(0,232,190,1) 100%); */
    background: linear-gradient(-45deg, #20cceb, #20cceb, #00ffc8, #23a6d5, #23d5ab);
    animation: gradient 15s ease infinite;
    background-size: 400% 400%;

    position: relative;

    background-color: turquoise;
    color: white;
    width: 330px;
    height: 200px;

    margin: auto;

    border-radius: 20px;

    box-shadow: inset 1px 2px 20px 6px rgb(34 60 80 / 20%);
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.card-num {
    font-size: 20px;
    line-height: 20px;
    height: 25px;
    position: absolute;
    top: 0; bottom: 0; left: 0; right: 0;
    margin: auto;
    letter-spacing: 5px;
    text-align: center;
}

.card-info {
    position: absolute;
    bottom: 10px;
    left: 10px;

    font-size: 15px;
    font-weight: 700;
}
.button {
    margin-top:10px;
    margin-left: 50%;
    transform: translateX(-50%);

    color: white;
    background-color: black;

    display: inline-block;

    height: 40px;
    padding: 10px;
    padding-left: 25px;
    padding-right: 25px;

    border-radius: 15px;
    border-style: none;

    cursor: pointer;
}

.disabled {
    background-color: rgb(199, 199, 199);
    color: gray;
}

.center {
    text-align: center;
}

.main-container {
    padding-top: 100px;
    margin: auto;
    max-width: 700px;
}

@media screen and (max-width: 700px) {
    .main-container {
        padding-left: 15px;
        padding-right: 15px;
        margin: auto;
        max-width: 700px;
    }
}

.partition-name {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 30px;
    margin-top: 30px;
}

.wide-card {
    width: 100%;
    background: rgb(0,158,226);
    /* background: linear-gradient(135deg, rgba(0,158,226,1) 0%, rgba(2,215,228,1) 35%, rgba(0,232,190,1) 100%); */
    background: linear-gradient(-45deg, #20cceb, #20cceb, #00ffc8, #2dbdf1, #23d5ab);
    animation: gradient 45s ease infinite;

    background-size: 400% 400%;
    position: relative;
    
    background-color: turquoise;
    color: white;
    height: 200px;
    
    margin: auto;
    
    border-radius: 20px;
    
    box-shadow: inset 1px 2px 20px 6px rgb(34 60 80 / 20%);
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.wc-amount {
    font-size: 20px;
    font-weight: 200;
    position: relative;
    top:30px;
    left:30px;
}

.wc-system {
    position: absolute;
    left:30px;
    bottom: 20px;

    font-weight: 600;
}

.transaction-card {
    border-radius: 20px;
    /* background-color: whitesmoke; */
    margin-bottom: 10px;

    display: grid;

    grid-template-columns: 75px 1fr 100px;

    grid-template-areas: 
    "img name amount"
    "img desc amount";
}

.ts-img {
    grid-area: img;
    
    background: rgb(222,222,222);
    
    width:75px;
    height:75px;

    border-radius: 20px;
}

.ts-name {
    grid-area: name;

    margin-top: 10px;
    margin-left: 10px;

    font-size: 18px;
    font-weight: 600;
}

.ts-desc {
    margin-left: 10px;
    position: relative;
    top:-10px;

    font-size: 17px;
    font-weight: 400;

    grid-area: desc;
}

.ts-amount {
    grid-area: amount;

    text-align: end;
    
    display: flex;
    justify-content: right;
    align-items: center;
}

.error-panel {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    color: white;
    background-color: red;
    border-radius: 15px;

    margin: auto;
    margin-top: 10px;
    padding: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
}

.success-panel {
    background-color: limegreen;
}

.red {
    color: red;
}

.green {
    color:#00CF21;
}

.big-font {
    font-weight: 600;
}

.input {
    border:none;
    border-bottom: 1px solid #777;
    width: 100%;
    font-size: 17px;
}

.input:focus {
    outline: none;
}

.tc-name {
    margin-top: 10px;
    font-size: 17px;
}

.subtext {
    width: 100%;
    text-align: center;
}

.link {
    color: black;
    text-decoration: none;
}

.link:hover {
    text-decoration: underline;
}

.link:visited {
    color: black;
}

.right {
    float: right;
}

.left {
    float: left;
}
footer {
    border-top:1px solid rgb(212, 212, 212);
    width: 100%;

    color:black;
}

footer>p {
    font-weight: 700;
    margin-top: 10px;
    margin-left: 10px;
}

footer>a {
    font-weight: 500;
    margin-top: 10px;
    margin-left: 10px;

    text-decoration: none;
}

footer>a:visited {
    color:black;
}
.topbar {
    position: fixed;
    width: 100vw;
    font-weight: 700;
    font-size: 20px;
    border-bottom: 1px solid rgb(212, 212, 212);
    text-align: center;
    padding: 15px;
    padding-left: 20px;
    background-color: rgba(255, 255, 255, 0.5);
    z-index: 1000;
    -webkit-backdrop-filter: blur(8px);
            backdrop-filter: blur(8px);
}

.login {
    cursor: pointer;
    font-weight: 500;
    font-size: 17px;
    position: relative;
    right:40px;
    top: 4px;
}

sup {
    font-weight: 400;
}
